.admin-container {
    max-width: 800px;
    margin: auto;
    padding: 2rem;
}
.product-card {
    border: 1px solid #ccc;
    padding: 10px;
    margin-bottom: 15px;
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    gap: 8px;
}
.product-card input,
.product-card textarea {
    padding: 8px;
    border-radius: 4px;
    border: 1px solid #999;
}
.product-card button {
    align-self: flex-start;
    padding: 6px 12px;
    border: none;
    background-color: #1976d2;
    color: white;
    border-radius: 4px;
    cursor: pointer;
}

.enquiry-list {
    list-style: none;
    padding: 0;
}
.dashboard-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logout-button {
    background-color: #e74c3c;
    color: white;
    border: none;
    padding: 8px 14px;
    font-size: 14px;
    border-radius: 5px;
    cursor: pointer;
}

.logout-button:hover {
    background-color: #c0392b;
}

.enquiry-card {
    background-color: #f5f5f5;
    padding: 1rem;
    margin-bottom: 1rem;
    border-left: 5px solid #007bff;
    border-radius: 6px;
}