/* Base Styles with Industrial Theme */
body {
    margin: 0;
    font-family: 'Roboto', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    background-color: #f5f7fa;
    color: #2d3748;
    line-height: 1.6;
    overflow-x: hidden;
}

/* Industrial Color Palette */
:root {
    --industrial-blue: #005b96;
    --industrial-dark: #1a365d;
    --steel-gray: #4a5568;
    --bearing-red: #c53030;
    --grease-yellow: #d69e2e;
    --mechanical-orange: #dd6b20;
    --factory-floor: #edf2f7;
    --metal-shine: rgba(255, 255, 255, 0.8);
}

/* Main Content */
.home-wrapper {
    padding-top: 80px; /* Space for fixed navbar */
    background-color: var(--factory-floor);
}

.home-content {
    max-width: 1400px;
    margin: 0 auto;
    padding: 2rem 5%;
}

/* Hero Section with Enhanced Bearing Animation */
.hero-section {
    text-align: center;
    padding: 4rem 0 6rem;
    position: relative;
    overflow: hidden;
    background: linear-gradient(135deg, #ffffff 0%, var(--factory-floor) 100%);
    border-radius: 10px;
    margin-bottom: 3rem;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.08);
}

.hero-section::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 5px;
    background: linear-gradient(to right, var(--bearing-red), var(--industrial-blue));
}

.hero-section h2 {
    font-size: 3rem;
    color: var(--industrial-dark);
    margin-bottom: 1.5rem;
    font-weight: 800;
    text-transform: uppercase;
    letter-spacing: -1px;
    position: relative;
    display: inline-block;
}

.hero-section h2 span {
    color: var(--industrial-blue);
    position: relative;
}

.hero-section h2 span::after {
    content: "";
    position: absolute;
    bottom: -8px;
    left: 0;
    width: 100%;
    height: 4px;
    background: linear-gradient(to right, var(--bearing-red), var(--industrial-blue));
    border-radius: 2px;
}

.hero-section p {
    max-width: 700px;
    margin: 0 auto 3rem;
    color: var(--steel-gray);
    font-size: 1.3rem;
    line-height: 1.7;
    font-weight: 500;
}

/* Bearing Animation Container */
.bearing-animation-container {
    position: relative;
    margin: 3rem auto;
    width: 220px;
    height: 220px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    perspective: 1000px;
}

/* Outer Ring - More Metallic Appearance */
.bearing-outer-ring {
    width: 200px;
    height: 200px;
    border: 15px solid transparent;
    border-radius: 50%;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    animation: rotate 12s linear infinite;
    background: 
        linear-gradient(145deg, #d1d1d1, #f8f8f8),
        linear-gradient(to right, 
            #a1a1a1 0%, 
            #d1d1d1 15%, 
            #f8f8f8 30%, 
            #d1d1d1 45%, 
            #a1a1a1 60%, 
            #d1d1d1 75%, 
            #f8f8f8 90%);
    background-origin: border-box;
    background-clip: padding-box, border-box;
    box-shadow: 
        0 10px 30px rgba(0, 0, 0, 0.2),
        inset 0 5px 15px rgba(0, 0, 0, 0.3),
        inset 0 -5px 15px rgba(255, 255, 255, 0.5);
    will-change: transform;
}

/* Inner Ring - With Groove Detail */
.bearing-inner-ring {
    width: 120px;
    height: 120px;
    border: 10px solid transparent;
    border-radius: 50%;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    animation: rotate 8s linear infinite reverse;
    background: 
        linear-gradient(145deg, #e6e6e6, #ffffff),
        linear-gradient(to right, 
            #005b96 0%, 
            #0081d6 50%, 
            #005b96 100%);
    background-origin: border-box;
    background-clip: padding-box, border-box;
    box-shadow: 
        inset 0 3px 10px rgba(0, 0, 0, 0.2),
        inset 0 -3px 10px rgba(255, 255, 255, 0.5),
        0 0 10px rgba(0, 91, 150, 0.5);
    will-change: transform;
}

/* Ball Bearings - With 3D Effect */
.bearing-balls {
    position: absolute;
    width: 100%;
    height: 100%;
    animation: rotate 24s linear infinite;
    will-change: transform;
}

.bearing-ball {
    position: absolute;
    width: 18px;
    height: 18px;
    border-radius: 50%;
    top: 50%;
    left: 50%;
    margin-left: -9px;
    margin-top: -9px;
    transform-style: preserve-3d;
    background: radial-gradient(
        circle at 30% 30%,
        #ffffff 0%,
        #d32f2f 70%,
        #8b0000 100%
    );
    box-shadow: 
        0 2px 5px rgba(0, 0, 0, 0.4),
        inset -3px -3px 5px rgba(0, 0, 0, 0.3),
        inset 2px 2px 5px rgba(255, 255, 255, 0.2);
    filter: drop-shadow(0 0 5px rgba(210, 47, 47, 0.5));
    will-change: transform;
}

/* Add the ball bearing positions */
.bearing-ball:nth-child(1) { transform: rotate(0deg) translateX(60px) rotate(0deg); }
.bearing-ball:nth-child(2) { transform: rotate(45deg) translateX(60px) rotate(-45deg); }
.bearing-ball:nth-child(3) { transform: rotate(90deg) translateX(60px) rotate(-90deg); }
.bearing-ball:nth-child(4) { transform: rotate(135deg) translateX(60px) rotate(-135deg); }
.bearing-ball:nth-child(5) { transform: rotate(180deg) translateX(60px) rotate(-180deg); }
.bearing-ball:nth-child(6) { transform: rotate(225deg) translateX(60px) rotate(-225deg); }
.bearing-ball:nth-child(7) { transform: rotate(270deg) translateX(60px) rotate(-270deg); }
.bearing-ball:nth-child(8) { transform: rotate(315deg) translateX(60px) rotate(-315deg); }

/* Company Showcase Blocks */
.company-showcase {
    display: flex;
    gap: 2rem;
    margin: 4rem 0;
}

.showcase-block {
    flex: 1;
    border-radius: 8px;
    overflow: hidden;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
    transition: all 0.4s cubic-bezier(0.25, 0.8, 0.25, 1);
    position: relative;
    height: 350px;
}

.showcase-block:hover {
    transform: translateY(-10px);
    box-shadow: 0 15px 40px rgba(0, 0, 0, 0.2);
}

.showcase-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.8s ease;
}

.showcase-block:hover .showcase-image {
    transform: scale(1.05);
}

.showcase-caption {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 1.5rem;
    background: linear-gradient(to top, rgba(0, 0, 0, 0.8), transparent);
    color: white;
}

.showcase-caption h3 {
    margin: 0 0 0.5rem;
    font-size: 1.5rem;
    font-weight: 700;
}

.showcase-caption p {
    margin: 0;
    font-size: 1rem;
    opacity: 0.9;
}

/* Call-to-Action Button */
.cta-button {
    margin-top: 3rem;
    background: linear-gradient(135deg, var(--industrial-blue) 0%, var(--industrial-dark) 100%);
    color: white;
    border: none;
    padding: 1.2rem 3rem;
    font-size: 1.2rem;
    border-radius: 6px;
    cursor: pointer;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 1.5px;
    transition: all 0.4s ease;
    position: relative;
    overflow: hidden;
    z-index: 1;
    box-shadow: 0 8px 25px rgba(0, 91, 150, 0.4);
}

.cta-button::before {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, transparent, var(--metal-shine), transparent);
    transition: 0.6s;
    z-index: -1;
}

.cta-button:hover::before {
    left: 100%;
}

.cta-button:hover {
    transform: translateY(-5px);
    box-shadow: 0 12px 30px rgba(0, 91, 150, 0.5);
}

/* Featured Products Section */
.featured-products {
    margin: 5rem 0;
    padding: 3rem 0;
    background-color: white;
    border-radius: 10px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.05);
    border-top: 5px solid var(--bearing-red);
    position: relative;
    overflow: hidden;
}

.featured-products::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 5px;
    background: linear-gradient(to right, var(--bearing-red), var(--industrial-blue), var(--grease-yellow));
}

.featured-products h3 {
    text-align: center;
    font-size: 2.2rem;
    color: var(--industrial-dark);
    margin-bottom: 3rem;
    position: relative;
    text-transform: uppercase;
    letter-spacing: 1.5px;
    padding-top: 1rem;
}

.featured-products h3::after {
    content: "";
    position: absolute;
    bottom: -15px;
    left: 50%;
    transform: translateX(-50%);
    width: 100px;
    height: 4px;
    background: linear-gradient(to right, var(--industrial-blue), var(--bearing-red));
    border-radius: 2px;
}

.products-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 2.5rem;
    padding: 0 2rem;
}

.product-card {
    background: white;
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.08);
    transition: all 0.4s cubic-bezier(0.25, 0.8, 0.25, 1);
    border: 1px solid #e2e8f0;
    position: relative;
}

.product-card::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 5px;
    background: linear-gradient(to right, var(--industrial-blue), var(--bearing-red));
    transform: scaleX(0);
    transform-origin: left;
    transition: transform 0.4s ease;
}

.product-card:hover::before {
    transform: scaleX(1);
}

.product-card:hover {
    transform: translateY(-12px);
    box-shadow: 0 15px 35px rgba(0, 0, 0, 0.15);
    border-color: var(--industrial-blue);
}

.product-image {
    width: 100%;
    height: 220px;
    object-fit: contain;
    padding: 1.5rem;
    background-color: #f8fafc;
    border-bottom: 1px solid #e2e8f0;
    transition: transform 0.6s ease;
}

.product-card:hover .product-image {
    transform: scale(1.1);
}

.product-card h4 {
    text-align: center;
    padding: 1.5rem;
    margin: 0;
    color: var(--industrial-dark);
    font-size: 1.3rem;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 1px;
    transition: color 0.3s ease;
}

.product-card:hover h4 {
    color: var(--bearing-red);
}

/* Address Box Styles */
.address-box {
    background: white;
    border-radius: 10px;
    padding: 2.5rem;
    margin: 4rem 0;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.08);
    border-top: 5px solid var(--bearing-red);
    position: relative;
    overflow: hidden;
}

.address-box h3 {
    text-align: center;
    font-size: 1.8rem;
    color: var(--industrial-dark);
    margin-bottom: 2.5rem;
    position: relative;
    text-transform: uppercase;
    letter-spacing: 1px;
}

.address-box h3::after {
    content: "";
    position: absolute;
    bottom: -10px;
    left: 50%;
    transform: translateX(-50%);
    width: 60px;
    height: 3px;
    background: var(--industrial-blue);
}

.address-content {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 2rem;
    margin-bottom: 3rem;
}

.address-item {
    display: flex;
    align-items: flex-start;
    gap: 1.2rem;
    padding: 1.5rem;
    background: rgba(0, 91, 150, 0.05);
    border-radius: 8px;
    transition: all 0.3s ease;
}

.address-item:hover {
    background: rgba(0, 91, 150, 0.1);
    transform: translateY(-5px);
}

.address-icon {
    color: var(--bearing-red);
    font-size: 1.5rem;
    min-width: 30px;
    margin-top: 3px;
}

.address-item p {
    margin: 0;
    color: var(--steel-gray);
    font-size: 1rem;
    line-height: 1.6;
}

.address-map {
    border-radius: 8px;
    overflow: hidden;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
    border: 1px solid #e2e8f0;
}

/* Responsive Adjustments */
@media (max-width: 1024px) {
    .hero-section h2 {
        font-size: 2.5rem;
    }
    
    .company-showcase {
        flex-direction: column;
    }
    
    .showcase-block {
        height: 300px;
    }
}

@media (max-width: 768px) {
    .hero-section {
        padding: 3rem 1rem 5rem;
    }

    .hero-section h2 {
        font-size: 2rem;
    }

    .hero-section p {
        font-size: 1.1rem;
    }

    .bearing-animation-container {
        width: 180px;
        height: 180px;
    }

    .bearing-outer-ring {
        width: 160px;
        height: 160px;
        border-width: 12px;
    }

    .bearing-inner-ring {
        width: 100px;
        height: 100px;
        border-width: 8px;
    }

    .bearing-ball {
        width: 14px;
        height: 14px;
        margin-left: -7px;
        margin-top: -7px;
    }

    .products-grid {
        grid-template-columns: 1fr;
    }

    .address-box {
        padding: 1.5rem;
    }
    
    .address-content {
        grid-template-columns: 1fr;
    }
    
    .address-item {
        padding: 1rem;
    }
}

/* Animation Keyframes */
@keyframes rotate {
    from { transform: rotate(0deg); }
    to { transform: rotate(360deg); }
}

@keyframes shine {
    from { background-position: -100px; }
    to { background-position: 200px; }
}